<template>
  <div>
    <div class="nav-bar">
      <h1 class="title">trendd</h1>
    </div>
    <div class="center-block">
      <div class="text-content">
        <h1 class="center-block-title">
          Discover the world's top technologies & trends.
        </h1>
        <p class="center-block-passage">
          Trendd is the leading destination to find & showcase technologies and
          trend's to the world's best professionals.
        </p>
      </div>
      <div class="image-block">
        <img
          class="image"
          src="../assets/undraw_lost_online_re_upmy.svg"
          alt="technology"
        />
      </div>
    </div>
    <div class="cards-block">
      <div class="cards" v-for="(post, item) in displayedPosts" :key="item">
        <vs-card>
          <template #title>
            <a :href="post.link">
              <h3>{{ post.title }}</h3>
            </a>
          </template>
          <template #img>
            <a :href="post.link">
              <img :src="post.img" alt="" />
            </a>
          </template>
          <template #text>
            <a :href="post.link">
              <p>{{ post.text }}</p>
            </a>
          </template>
        </vs-card>
      </div>
    </div>
    <div class="pagination-block">
      <vs-pagination
        infinite
        v-model="page"
        :length="maxlength"
        @input="loadNext"
      />
    </div>
    <div class="footer">
      <p class="footer-title">© 2021 Trendd. All rights reserved.</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Trendd",
  data: () => ({
    posts: [],
    page: 1,
    perPage: 9,
    maxlength: 2,
    pages: [],
    color: "success",
  }),
  methods: {
    getBlogs() {
      this.posts = require("./posts/blogs.json");
    },
    loadNext() {
      this.toRender = [];
      for (let i = this.start; i < this.end; i++) {
        console.log(i);
        this.toRender.push(this.posts[i]);
      }
    },
    setPages() {
      let numberOfPages = Math.ceil(this.posts.length / this.perPage);
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    },
    paginate(posts) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return posts.slice(from, to);
    },
  },
  created() {
    this.getBlogs();
    this.maxlength = Math.ceil(this.posts.length / this.perPage);
  },
  watch: {
    posts() {
      this.setPages();
    },
  },
  computed: {
    displayedPosts() {
      return this.paginate(this.posts);
    },
  },
};
</script>

