import Vue from "vue";
import VueRouter from "vue-router";
import Trendd from '@/components/TRENDD';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Trendd',
    component: Trendd
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
